<script>
import Bus from '@/components/Data/bus.js'
import SingleUploadImage from '@/components/Core/SingleUploadImage'


export default {
    components: {
      SingleUploadImage,
    },
    props:{
      patientDetail: {
          type: Object,
          default: {}
      }
    },
    data() {
      return {
        showEdit: false,
        generalCheck: {
          chiefComplaint: '',
          badHabits: '',
          oralHygiene: '',
          cervicalVertebra: '',
          wristBone: '',
          menstruation: '',
          menarche: '',
          correctiveHistory: '',
          attendingDoctor: '',
          toothExtraction: '',
          dentalCaries: '',
          earlyLoss: '',
          malocclusion: '',
          dentalCariesReplace: '',
          earlyLossReplace: '',
          malocclusionReplace: '',
          cleftLip: '',
          cleftPalate: '',
          others: ''
        },
        generalCheckRules: {},
        faceCheck: {
          positive: '',   //
          positiveSmile: '',  //
          side: '', //
          right: '',  //
          left: '',  //
          other: '', //
          frontalType: '',
          chinSocket: '',
          lipShape: '',
          symmetry: '',
          smilePulling: '',
          relaxPulling: '',
          faceShape: '',
          nasolabialAngle: '',
          labialGroove: '',
          chinPosition: '',
          others: '',
        },
        faceCheckRules: {},
        softCheck: {
          maxilla: '',
          mandible: '',
          mandibularAngle: '',
          tongueBody: '',
          upperLip: '',
          lowerLip: '',
          lingualFrenulum: '',
          labialFrenulum: '',
          tonsil: '',
          adenoid: '',
          palatalLid: '',
          painLeft: '',
          painRight: '',
          bangLeft: '',
          bangRight: '',
          openMouth: '',
          closeMouth: '',
          aperture: '',
          respiratoryTract: '',
          degree: '',
          other: '',
        },
        softCheckRules: {

        },
        dentitionCheck: {
          frontUrl: '',
          upUrl: '',
          leftUrl: '',
          frontTeethUrl: '',
          downUrl: '',
          rightUrl: '',
          otherUrl: '',
          upTeeth: '',
          downTeeth: '',
          moreTeeth: '',
          loss: '',
          dislocation: '',
          anteriorRatio: '',
          overallRatio: '',
          leftMolar: '',
          leftFangs: '',
          anteriorTeeth: '',
          rightMolar: '',
          rightFangs: '',
          archForm: '',
          posteriorTeeth: '',
          canineTeeth: '',
          maxillaryOverbite: '',
          maxillary: '',
          other: '',
          mandible: '',

        },
        dentitionCheckRules: {},
        showPrint: false,


      }
    },
    mounted() {
      this.getGeneralCheck()
      this.getFaceCheck()
      this.getSoftCheck()
      this.getDentitionCheck()
      console.log(this.patientDetail);

      Bus.$on('singleUpload', (e) => {
        if (e.name == 'positive') {
          this.faceCheck.positive = e.url
        }
        if (e.name == 'positiveSmile') {
          this.faceCheck.positiveSmile = e.url
        }
        if (e.name == 'side') {
          this.faceCheck.side = e.url
        }
        if (e.name == 'right') {
          this.faceCheck.right = e.url
        }
        if (e.name == 'left') {
          this.faceCheck.left = e.url
        }
        if (e.name == 'other') {
          this.faceCheck.other = e.url
        }

        if (e.name == 'frontUrl') {
          this.dentitionCheck.frontUrl = e.url
        }
        if (e.name == 'upUrl') {
          this.dentitionCheck.upUrl = e.url
        }
        if (e.name == 'leftUrl') {
          this.dentitionCheck.leftUrl = e.url
        }
        if (e.name == 'frontTeethUrl') {
          this.dentitionCheck.frontTeethUrl = e.url
        }
        if (e.name == 'downUrl') {
          this.dentitionCheck.downUrl = e.url
        }
        if (e.name == 'rightUrl') {
          this.dentitionCheck.rightUrl = e.url
        }
        if (e.name == 'otherUrl') {
          this.dentitionCheck.otherUrl = e.url
        }
      })
    },
    methods: {

      //
      submitGeneralCheck(formName) {
        let params = this.generalCheck
        params.patientNo = this.patientDetail.code

        if (this.generalCheck.code) {
          params.code = this.generalCheck.code

          this.$http.post("/api/hx/generalCheck/edit.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getGeneralCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        } else {
          this.$http.post("/api/hx/generalCheck/add.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getGeneralCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        }

      },

      //
      submitFackCheck() {
        let params = this.faceCheck
        params.patientNo = this.patientDetail.code
        console.log(params)

        if (this.faceCheck.code) {
          params.code = this.faceCheck.code

          this.$http.post("/api/hx/faceCheck/edit.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getFaceCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        } else {
          this.$http.post("/api/hx/faceCheck/add.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getFaceCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        }
      },

      //
      submitSoftCheck() {
        let params = this.softCheck
        params.patientNo = this.patientDetail.code
        console.log(params)

        if (this.softCheck.code) {
          params.code = this.softCheck.code

          this.$http.post("/api/hx/softCheck/edit.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getSoftCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        } else {
          this.$http.post("/api/hx/softCheck/add.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getSoftCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        }
      },

      //
      submitDentitionCheck() {
        let params = this.dentitionCheck
        params.patientNo = this.patientDetail.code
        console.log(params)

        if (this.dentitionCheck.code) {
          params.code = this.dentitionCheck.code

          this.$http.post("/api/hx/dentitionCheck/edit.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getDentitionCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        } else {
          this.$http.post("/api/hx/dentitionCheck/add.do",params).then(res => {
            if (res.data.result) {
              this.$message.success('')
              this.showEdit = false
              this.getDentitionCheck()
            } else {
              this.$message.error(res.data.errorMessage)
            }
          })
        }
      },

      //
      getGeneralCheck() {
        this.$http.post("/api/hx/generalCheck/get.do",{
          patientNo: this.patientDetail.code
        }).then(res => {
          if (res.data.result) {
              this.generalCheck = res.data.returnObject || {}
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },

      //
      getFaceCheck() {
        this.$http.post("/api/hx/faceCheck/get.do",{
          patientNo: this.patientDetail.code
        }).then(res => {
          if (res.data.result) {
              this.faceCheck = res.data.returnObject || {}
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },

      //
      getSoftCheck() {
        this.$http.post("/api/hx/softCheck/get.do",{
          patientNo: this.patientDetail.code
        }).then(res => {
          if (res.data.result) {
              this.softCheck = res.data.returnObject || {}
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },

      //
      getDentitionCheck() {
        this.$http.post("/api/hx/dentitionCheck/get.do",{
          patientNo: this.patientDetail.code
        }).then(res => {
          if (res.data.result) {
              this.dentitionCheck = res.data.returnObject || {}
          } else {
            this.$message.error(res.data.errorMessage)
          }
        })
      },

      editCheck() {
        this.showEdit = true
      },

      //
      print() {
        this.$print(this.$refs.print)
      },

    },
}
</script>

<style lang="less" scoped>
  .headBox{
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin: 10px 0;
    padding: 0 20px;
    box-sizing: border-box;
    background-color: #d1d1d1;
  }
  .cf{
    clear: both;
    overflow: hidden;
    content: '';
  }
  .formItem{
    width: 90px;
    display: inline-block;
    height: 40px;
    line-height: 40px;
    font-weight: 550
  }
  .imgItemBox{
    text-align: center;
  }
  .imgItem{
    width: 120px;
    max-height: 120px;
  }
  .title{
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
  }
  .printHead{
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    height: 60px;
    line-height: 60px;
    border-top: 4px solid #000;
    border-bottom: 2px solid #000;
  }
  .printTitle{
    text-align: left;
    font-weight: 550;
    font-size: 18px;
    margin: 30px 0 30px 0;
  }

  .printBox{
    border-bottom: 1px solid #d1d1d1;
  }
</style>



