<template>
  <div>
    <span slot="label" style="font-size: 18px">
      <i class="el-icon-time" style="margin-right: 10px"></i>
    </span>
    <br>
    <br>
    <el-table :data="PendingList" style="width: 100%">
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.process === 'UNCHECK'"><span class="indicatorA"><span></span></span></span>
          <span v-if="scope.row.process === 'UNPAY'"><span class="indicatorB"><span></span></span></span>
          <span v-if="scope.row.process === 'UNPASS'"><span class="indicatorA"><span></span></span></span>
          <span v-if="scope.row.process === 'UNREFUND'"><span class="indicatorC"><span></span></span></span>
          <span v-if="scope.row.process === 'FINISH'"><span class="indicatorD"><span></span></span></span>
        </template>
      </el-table-column>
      <el-table-column prop="merchantName" label=""></el-table-column>
      <el-table-column prop="memberNameGuid" label=""></el-table-column>
      <el-table-column prop="createDate" label="" width="150"></el-table-column>
      <el-table-column prop="clinicTime" label="" width="150"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.operateType === 'PAY'"></span>
          <span v-if="scope.row.operateType === 'DEBT'"></span>
          <span v-if="scope.row.operateType === 'REFUND'"></span>
        </template>
      </el-table-column>
      <el-table-column prop="doctorName" label=""></el-table-column>
      <el-table-column prop="originalAmount" label="">
        <template
          slot-scope="scope"
        >{{scope.row.originalAmount? scope.row.originalAmount / 100 : ''}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.reallyAmount / 100}}</template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.payAmount / 100}}</template>
      </el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
      <el-table-column label="" width="200" fixed="right">
        <template slot-scope="scope">
          <!-- <el-button type="text" @click="handleEditPeddingItem(scope.row)"></el-button> -->
          <el-button
            type="text"
            v-if="scope.row.operateType === 'PAY' && scope.row.process === 'UNPAY'"
            @click="handleCharge(scope.row)"
          ></el-button>
          <el-button
            type="text"
            v-if="scope.row.operateType === 'DEBT' "
            @click="handleCollectOverdueBill(scope.row)"
          ></el-button>
          <el-button
            type="text"
            v-if="scope.row.operateType === 'REFUND' && scope.row.process === 'UNREFUND'"
            @click="handleReturnCharge(scope.row)"
          ></el-button>
          <el-button type="text" @click="handleCancelPendingItem(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <br>
    <el-pagination
      @current-change="handlePageQuery"
      :current-page="currentPage"
      :page-size="limit"
      background
      layout="total, prev, pager, next"
      :total="total"
    ></el-pagination>
    <!--  -->
     <el-dialog
        title=""
        :visible.sync="dialogVisible_returncharge"
        width="30%">
        <br>
         : <span v-if="billOperate ? billOperate.refundType ==='ALL' : ''"></span><span v-if="billOperate? billOperate.refundType ==='PART' : ''"></span>
        <br><br>
        <el-table
            :data="billProjectList"
            ref="tableCheckbox"
            >
            <el-table-column prop="projectName" label="" :key="Math.random()"></el-table-column>
            <el-table-column label="" :key="Math.random()">
                <template slot-scope="scope">
                   {{scope.row.unitPrice / 100}}
                </template>
            </el-table-column>
            <el-table-column prop="itemNum" label="" :key="Math.random()"></el-table-column>
            <el-table-column label="" :key="Math.random()">
                <template slot-scope="scope">
                   {{scope.row.itemDiscountAmount / 100}}
                </template>
            </el-table-column>
             <el-table-column label="" :key="Math.random()">
                <template slot-scope="scope">
                    {{scope.row.currentRtAmount}}
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{scope.row.itemNum - scope.row.rtNum}}
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <span>{{scope.row.currentRtNum}}</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="totalBox">
            <span>{{billDetail.reallyAmount / 100}}</span>
            <span>{{billDetail.payAmount / 100 }}</span>
            <span>{{ retuRecevied(billOperate) / 100}}</span>
            <span>{{backReallyAmount / 100}}</span>
            <span>{{backPayAmount / 100}}</span>
        </div>
        <div class="returnInfo">
            <span>: {{patientDetail.name}}</span>
            <!-- <span>: {{billDetail.remark2}}</span> -->
            <span>: <i>{{retuRecevied(billOperate) /100 }}</i></span>
        </div>
        <el-form :inline="true" ref="formReturnAmount" label-width="100px" :model="returnChargeForm" :rules="returnChargeFormRules" style="width: 100%;margin: 30px 0;" class="demo-form-inline">
            <el-form-item label="" prop="payType">
                <el-select v-model="returnChargeForm.payType" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option v-for="item in payTypeList" :key="item.code" :label="item.lableName" :value="item.code"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=""  prop="refundTimeStr">
                <el-date-picker
                    v-model="returnChargeForm.refundTimeStr"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder=""
                    v-model="returnChargeForm.remark">
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible_returncharge = false"></el-button>
            <el-button type="primary" v-if="optType ==='REFUND'" @click="handleConfirmReturn"></el-button>
            <el-button type="primary" v-if="optType ==='SAVE'" @click="handleConfirmReturn"></el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
import storage from "@/tools/local_storage.js";

export default {
  components: {},
  props: {
    patientDetail: {
      type: Object,
      default: {}
    },
    payTypeList: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      dialogVisible_returncharge: false,
      billDetail: {},
      billOperate: {},
      billProjectList: [],
      PendingList: [],
      optType: 'SAVE',
      currentPage: 1,
      limit: 10,
      total: 1,
      returnChargeForm: {
        payType: '',
        refundTimeStr: '',
        remark: ''
      },
      returnChargeFormRules: {
        payType:[
          { required: true, message: '', trigger: 'change' },
        ],
        refundTimeStr:[
          { required: true, message: '', trigger: 'change' },
        ]
      }
    }
  },
  watch: {},
  computed: {

    // 
    // returnReallyAmount: (_this) => {
    //    // 
    //    if( _this.refundType === 'PART') {
    //       return _this.billOperate.payAmount
    //    } else {
    //       //
    //       return  _this.billDetail.payAmount
    //    }
    // },

    // 
    backReallyAmount: (_this) => {

        // 
        if( _this.billOperate.refundType === 'PART') {

          // 20190523 
          return _this.subtractMath(_this.billOperate.billPayAmount, _this.billOperate.payAmount)
            // // 
            // let amount = _this.subtractMath(_this.billDetail.payAmount,_this.returnReallyAmount)
            // // 0-
            // if( amount >= Number(_this.billDetail.reallyAmount ) ) {
            //     return 0
            // } else {
            //     return _this.subtractMath(_this.billDetail.reallyAmount,amount)
            // }

        } else {
           // 
           return _this.billDetail.reallyAmount
        }

    },

    // 
    backPayAmount: (_this) => {

       // 
       if( _this.refundType === 'PART') {
            // 
            let amount = _this.subtractMath(_this.billDetail.payAmount,_this.billOperate.payAmount)

            //  > 
            if(  amount > 0) {
                return amount
            } else {
                return 0
            }
       } else {

          // 
          return 0
       }
    }

  },
  methods: {

      // 
      handlePageQuery(currentPage) {
        this.currentPage = currentPage
        this.getFollowupList()
      },

      // (/)
      handleEditPeddingItem(row) {
        // 
        if( row.operateType === 'REFUND' ) {
          this.getBillDetail(row,()=>{
            this.optType = 'SAVE'
            this.dialogVisible_returncharge = true
          })
        }
        // 
        else if ( row.operateType === 'PAY'){
          this.$emit('showCreateBill',row,'edit')
        }
        // 
        else if( row.operateType === 'DEBT'){
          // this.$emit('showCollection',row,'edit')
           this.$emit('showCollection',row,'edit', true)
        }
      },

      // 
      handleCharge(row) {
        this.$emit('showCreateBill',row,'edit')
      },

      // 
      handleCollectOverdueBill(row) {
        this.$emit('showCollection',row,'edit', true)
      },

      // 
      handleReturnCharge(row) {
        this.getBillDetail(row,()=>{
          this.optType = 'REFUND'
          this.dialogVisible_returncharge = true
        })
      },

      // 
      handleCancelPendingItem(row) {
         this.$confirm('?', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
          }).then(() => {
              this.$http.post('/api/hx/bill/untreatedCancel.do',{
                code: row.code
              }).then(res=>{
                  const { result, errorMessage } =  res.data
                  if(result){
                      this.$message({
                          type: "success",
                          message: '!'
                      })
                      this.getPendingList()
                  }else{
                      this.$message({
                      type:'error',
                      message: errorMessage
                      })
                  }
              })
          }).catch(() => {
              this.$message({
                  type: 'info',
                  message: ``
              })
          })
      },

      // /
      handleConfirmReturn() {
        console.log(this.billDetail.billOperate);
        return false
         const { refundCode ,recieverNo,recieverName,memberNoGuid,memberNameGuid} = this.billDetail.billOperate

         const { payType,refundTimeStr ,remark} = this.returnChargeForm
         this.$http.post('/api/hx/bill/refund.do',{
           code:refundCode,
           optType: this.optType,
           payType,
           payTypeName: this.payTypeList.find(item=>item.code === payType)['lableName'],
           refundGdNo:recieverNo,
           refundGdName:recieverName,
           memberNoGuid,
           memberNameGuid,
           refundTimeStr:refundTimeStr,
           remark
         }).then(res=>{
            const { result, errorMessage, returnObject } = res.data
            if (result) {
                this.getPendingList()
                this.dialogVisible_returncharge = false
                if(this.optType === 'SAVE') {
                    this.$message({
                        type: "success",
                        message: ''
                    })
                 } else{
                    this.$message({
                        type: "success",
                        message: ''
                    })
                 }
            } else {
                this.$message({
                    type: "error",
                    message: errorMessage
                });
            }
         })
      },



      // 
      getBillDetail(row,callback) {
        const {
                memberNoGuid,
                memberNoMerchant,
                memberNameMerchant,
                roleEnnames,
                shopNo
          } = storage.get("userInfo")
          this.$http.post('/api/hx/bill/detail.do',{
              merchantNo:memberNoMerchant,
              code:   row.code,
              billCode: row.billCode
          }).then(res=>{
                const { result, errorMessage, returnObject } =  res.data
                if(result){
                    this.billDetail = returnObject
                    this.billOperate = returnObject.billOperate
                    this.billProjectList =  returnObject.details

                    if( this.optType === 'SAVE' ) {
                      this.returnChargeForm= {
                        payType: returnObject.billOperate.payType,
                        refundTimeStr:  returnObject.billOperate.payTime,
                        remark:  returnObject.billOperate.remark,
                      }
                    }

                    if(callback)callback()
                }else{
                    this.$message({
                      type:'error',
                      message: errorMessage
                    })
                }
          })
      },

      // 
      getPendingList() {
            const { currentPage , limit} = this
            const { code } = this.patientDetail
            const {
                    memberNoGuid,
                    memberNoMerchant,
                    memberNameMerchant,
                    roleEnnames,
                    shopNo
            } = storage.get("userInfo")
            this.$http.post('/api/hx/bill/untreatedList.do',{
                start: (currentPage - 1) * limit,
                limit: limit,
                merchantNo: memberNoMerchant,
                patientNo: code
            }).then(res=>{
                    const { result, errorMessage, returnObject } = res.data;
                    if (result) {
                        const { total, rows } = returnObject
                        this.PendingList = rows
                        this.total = total
                    } else {
                        this.$message({
                            type: "error",
                            message: errorMessage
                        })
                    }
            })
      },

      // 20190523
     //  
     retuRecevied (obj) {
       if (obj.refundType == 'PART') {
         // 
         return obj.payAmount
       } else {
         //
         return this.billDetail.payAmount
       }
     },


  },
  created() {},
  mounted() {
    this.getPendingList()
  }
};
</script>
<style lang="less" scoped>
.totalBox{
    text-align: center;
    span{
        display: inline-block;
        margin: 20px 30px 20px 0;
        font-size: 18px;
    }
}
.returnInfo{
    padding: 30px 0;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eeeeee;
    font-size: 16px;
    text-align: center;
    span{
        display: inline-block;
        margin-right: 100px;
        i{
            font-size: 24px;
            color: red;
        }
    }
}
</style>



