<template>
  <div class="PatientInfoBox" style="border-bottom: none">
    <el-form :model="localPatientDetail" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <br>
    <div> 
      <el-button size="large" v-if="!isEdit" type="success" @click="handleEditPatinet"></el-button>
      <el-button size="large" v-if="isEdit" style="margin-right: 10px" @click="handleEditCancel"></el-button>
      <el-button size="large" v-if="isEdit" type="success" @click="handleSavePatinet('ruleForm')"></el-button>
    </div>
    <br><br>
    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
         <span v-if="!isEdit">: {{patientDetail.name}}</span>
         <el-form-item label=":" v-if="isEdit" prop="name">
            <el-input v-model="localPatientDetail.name"></el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.type === 'PT' ? '' :''}}</span>
        <el-form-item label=":"  v-if="isEdit">
          <el-radio-group v-model="localPatientDetail.type" size="small">
            <el-radio-button label="PT" value="PT"></el-radio-button>
            <el-radio-button label="LS" value="LS"></el-radio-button>
          </el-radio-group>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.birthDate}}</span>
        <el-form-item label=":" v-if="isEdit">
          <el-date-picker 
            v-model="localPatientDetail.birthDate" 
            type="date" 
            value-format="yyyy-MM-dd"
            @change="handleGetAge('age',localPatientDetail.birthDate)"
            placeholder="">
          </el-date-picker>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.age}}</span>
         <el-form-item label=":" v-if="isEdit">
            <el-input v-model="localPatientDetail.age" placeholder="()"></el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
         <span v-if="!isEdit">: <el-tag size="mini" v-if="patientDetail.sex === 'MALE'"></el-tag>
              <el-tag size="mini" type="danger" v-if="patientDetail.sex === 'FEMALE'"></el-tag>
              <el-tag size="mini"  v-if="patientDetail.sex === 'UNKNOWN'"></el-tag></span>
         <el-form-item label=":" v-if="isEdit">
            <el-radio-group v-model="localPatientDetail.sex">
              <el-radio label="MALE"></el-radio>
              <el-radio label="FEMALE"></el-radio>
              <el-radio label="UNKNOWN"></el-radio>
            </el-radio-group>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.idno}}</span>
        <el-form-item label=": " v-if="isEdit">
          <el-input v-model="localPatientDetail.idno" :disabled=" localPatientDetail.nationality ===''"  placeholder=""></el-input>
        </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.nationality}}</span>
         <el-form-item label=":" v-if="isEdit">
            <el-select v-model="localPatientDetail.nationality" placeholder="">
                <el-option
                  key=""
                  label=""
                  value=""
                ></el-option>
                <el-option
                  key=""
                  label=""
                  value=""
                ></el-option>
              </el-select>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin:10px 0">
        <span v-if="!isEdit">:{{patientDetail.remark}}</span>
         <el-form-item label=":" v-if="isEdit">
            <el-input type="textarea" v-model="localPatientDetail.remark" placeholder=""></el-input>
         </el-form-item>
      </el-col>
    </el-row>

    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
         <span v-if="!isEdit">: {{patientDetail.phone}}</span>
         <el-form-item label=":" v-if="isEdit">
           <el-input placeholder="" v-model="localPatientDetail.phone" class="input-with-select">
            <el-select v-model="localPatientDetail.phoneRemark" slot="append" placeholder="" style="width:80px">
                <el-option key="" label="" value=""></el-option>
                <el-option key="" label="" value=""></el-option>
                <el-option key="" label="" value=""></el-option>
                <el-option key="" label="" value=""></el-option>
            </el-select>
          </el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">:  {{patientDetail.phoneNo}}</span>
         <el-form-item label=": " v-if="isEdit">
           <el-input placeholder="" v-model="localPatientDetail.phoneNo" class="input-with-select">
            <el-select v-model="localPatientDetail.phoneNoRemark" slot="append" placeholder="" style="width:80px">
                <el-option key="" label="" value=""></el-option>
                <el-option key="" label="" value=""></el-option>
            </el-select>
          </el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
         <span v-if="!isEdit">:{{patientDetail.wechat}}</span>
         <el-form-item label=":" v-if="isEdit">
            <el-input v-model="localPatientDetail.wechat"  placeholder=""></el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">QQ:{{patientDetail.qqNo}}</span>
         <el-form-item label="QQ:" v-if="isEdit">
            <el-input v-model="localPatientDetail.qqNo"  placeholder="QQ"></el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">:{{patientDetail.mail}}</span>
         <el-form-item label=":" v-if="isEdit">
            <el-input v-model="localPatientDetail.mail"  placeholder=""></el-input>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">
        <span v-if="!isEdit">: {{patientDetail.province}}{{patientDetail.city}}{{patientDetail.area}}{{patientDetail.addrDetail}}</span>
        <el-form-item label=":" v-if="isEdit">
            <el-cascader
              size="large"
              :options="options"
              v-model="selectedOptions">
            </el-cascader>
         </el-form-item>
      </el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0" v-if="isEdit">
        <el-form-item label=":">
             <el-input type="textarea" v-model="localPatientDetail.addrDetail"  placeholder=""></el-input>
        </el-form-item>
      </el-col>
    </el-row>
    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      <el-col :xs="24" :sm="24" :md="24" :lg="24" :xl="24" style="margin:10px 0">
        <span v-if="!isEdit">: {{ patientDetail.source1}} {{patientDetail.source2 ? '/' + patientDetail.source2 : ''}} {{ patientDetail.source3 ? '/' + patientDetail.source3 : ''}} </span>
        <el-form-item label=": " v-if="isEdit">
            <el-select 
            v-model="localPatientDetail.source1Code" 
            placeholder=""
            @change="handleItemChange('source1Code',localPatientDetail.source1Code)"
          >
               <el-option
                  label=""
                  value=""
                ></el-option>
                <el-option
                  v-for="item in appVuex.user_source"
                  :key="item.code"
                  :label="item.lableName"
                  :value="item.code"
                ></el-option>
            </el-select>
            <el-select v-model="localPatientDetail.source2Code" @change="handleItemChange('source2Code',localPatientDetail.source2Code)" placeholder="">
                <el-option
                  label=""
                  value=""
                ></el-option>
                <el-option
                  v-for="item in source2"
                  :key="item.code"
                  :label="item.lableName"
                  :value="item.code"
                ></el-option>
              </el-select>
              <el-select v-model="localPatientDetail.source3Code" @change="handleItemChange('source3Code',localPatientDetail.source3Code)" placeholder="">
                <el-option
                  label=""
                  value=""
                ></el-option>
                <el-option
                  v-for="item in source3"
                  :key="item.code"
                  :label="item.lableName"
                  :value="item.code"
                ></el-option>
              </el-select>
        </el-form-item>
            
      </el-col>
    </el-row>
    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.dutyMemberName}}</el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.consMemberName}}</el-col>
    </el-row>
    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.pastHistory}}</el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.allergyHistory}}</el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.medicationHistory}}</el-col>
    </el-row>
    <div class="formTitle">
      <span></span>
    </div>
    <el-row :gutter="10" class="PatientInfoBox">
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.createTime}}</el-col>
      <el-col :xs="24" :sm="12" :md="8" :lg="8" :xl="8" style="margin:10px 0">: {{patientDetail.firstMemberName}}</el-col>
    </el-row>
     </el-form>
  </div>
</template>

<script>
import moment from 'moment'
import { mapState,mapActions } from "vuex";
import storage from '@/tools/local_storage.js'
import { regionData, CodeToText} from 'element-china-area-data'
import { _mmAddress,_mmGetAge } from '@/utils/mm.js'
export default {
  components: {},
  props: {
    patientDetail:{
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      isEdit: false,
      source2: [],
      source3: [],
      options: regionData,
      selectedOptions: ["150000", "150400", "150421"],
      localPatientDetail: {
        name: '',
        type: '',
        birthDate: '',
        age: '',
        sex: '',
        idno: '',
        nationality: '',
        remark: '',
        phone: '',
        phoneRemark: '',
        phoneNo: '',
        phoneNoRemark: '',
        wechat: '',
        qqNo: '',
        mail: '',
        provinceCode:'',
        cityCode:'',
        areaCode:'',
        addrDetail: '',
        source1Code: '',
        source2Code: '',
        source3Code: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        type: [
          { required: true, message: '', trigger: 'blur' }
        ],
        phone:[
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  watch: {},
  computed: {
    ...mapState({
       appVuex: state => state["appVuex"]
    }),
  },
  methods: {

    // 
    handleEditPatinet() {
      const { source1Code, source2Code , source3Code}  = this.patientDetail
      const arr = []
      for(let key in this.patientDetail){
        this.localPatientDetail[key] = this.patientDetail[key]
      }
      arr.push(this.patientDetail['provinceCode'])
      arr.push(this.patientDetail['cityCode'])
      arr.push(this.patientDetail['areaCode'])
      this.selectedOptions = arr

      this.isEdit = true
      
      if( source1Code ) {
        this.getUserSourceList(source1Code,2,false)
      } 

      if( source2Code ) {
        this.getUserSourceList(source2Code,3,false)
      }
    },

    // 
    handleSavePatinet(formName) {
      const data = _mmAddress(this.localPatientDetail,this.selectedOptions,CodeToText)
      if( data.source1Code){
        data['source1'] = this.appVuex.user_source.find(item=> item.code === data.source1Code)['lableName']
      } else {
        data['source1'] = ''
      }
      if( data.source2Code){
        data['source2'] = this.source2.find(item=> item.code === data.source2Code)['lableName']
      } else {
        data['source2'] = ''
      }
      if( data.source3Code){
        data['source3'] = this.source3.find(item=> item.code === data.source3Code)['lableName']
      } else{
        data['source3'] = ''
      } 
      this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post('/api/hx/patient/save.do',{
               ...data
            }).then(res=>{
                const { result, errorMessage } = res.data
                if( result ) {
                  this.$message({
                    type:'success',
                    message: ''
                  })
                  this.$emit('getPatientDetail')
                  this.isEdit = false
                }else {
                  this.$message({
                    type:'error',
                    message: errorMessage
                  })
                }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
       });
    },

    // 
    handleGetAge(key,val) {
      console.log(key,val)
      if( val ) {
         this.localPatientDetail[key] = _mmGetAge(val)
      } else {
         this.localPatientDetail[key] = val
      }
    },

    // 
    handleEditCancel() {
      this.isEdit = false
    },
    
    // 
    getUserSourceList(parentCode,key,isClear) {
      const { shopNo, memberNoMerchant } = this.$cfg.userInfo || storage.get("userInfo");
      this.$http.post("/api/hx/configList.do", {
          shopNo,
          merchantNo: memberNoMerchant,
          lableNo: "user_source",
          parentCode
        }).then(res => {
          const { returnObject } = res.data;
          if( key === 2) {
            if( isClear){
              this.localPatientDetail.source2Code = ''
              this.localPatientDetail.source3Code = ''
            }
            this.source2 = returnObject;
            this.source3 = []
          }
          if( key === 3) {
             if( isClear){
              this.localPatientDetail.source3Code = ''
            }
            this.source3 = returnObject;
          }
        })
    },

    // 
    handleItemChange(key,val) {
      if( key === "source1Code" && val){
         this.getUserSourceList(val,2,true)
      }
      if( key === "source2Code" && val){
         this.getUserSourceList(val,3,true)
      }
    },
  },
  created() {},
  mounted() {}
};
</script>
<style lang="less" scoped>
.PatientInfoBox{
  width: 100%;
}
</style>


