<template>
  <div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible_billprint"
        width="30%">
        <div id="wrap_a" ref="printBill" class="printBox wrap">
         <div style="font-size: 18px;text-align:center">{{billDetail.merchantName}}</div>
         <br>
         <table>
             <tr>
                 <td colspan="2" style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;">
                     <span>{{patientDetail.caseNo}}</span>
                 </td>
                 <td colspan="2" style="border-top: 1px solid #eeeeee;">
                     <span>{{patientDetail.name}}</span>
                 </td>
                 <td colspan="2" style="border-top: 1px solid #eeeeee;border-right: 1px solid #eeeeee;">
                     <span>{{billDetail.billNo}}</span>
                 </td>
             </tr>
             <tr>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;"></td>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;"></td>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;"></td>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;"></td>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;">%</td>
                 <td style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-bottom: 1px solid #eeeeee;border-right: 1px solid #eeeeee;"></td>
             </tr>
             <tr v-for="(item,index) in billDetail.details" :key="index">
                 <td style="border-left: 1px solid #eeeeee;">{{item.projectName}}</td>
                 <td>{{item.unitPrice / 100}}</td>
                 <td>{{item.itemNum}}</td>
                 <td>{{item.projectUnit}}</td>
                 <td>{{item.itemDisUnitPrice / 100 }}</td>
                 <td style="border-right: 1px solid #eeeeee;">{{item.itemDiscountAmount / 100}}</td>
             </tr>
             <tr>
                 <td style="border-left: 1px solid #eeeeee;border-right: 1px solid #eeeeee;" height="60" colspan="6"></td>
             </tr>
             <tr>
                 <td colspan="2" style="border-top: 1px solid #eeeeee;border-left: 1px solid #eeeeee;">
                     <span>{{billDetail.originalAmount / 100}}</span>
                 </td>
                 <td colspan="2" style="border-top: 1px solid #eeeeee">
                     <span>{{billDetail.discountNum / 100}}</span>
                 </td>
                 <td colspan="2" style="border-top: 1px solid #eeeeee;border-right: 1px solid #eeeeee;">
                     <span>{{billDetail.reallyAmount / 100}}</span>
                 </td>
             </tr>
             <tr>
                 <td colspan="2" style="border-left: 1px solid #eeeeee;">
                     <span>{{subtractMath(billDetail.originalAmount,billDetail.reallyAmount) / 100}}</span>
                 </td>
                 <td colspan="2">
                     <span>{{billDetail.debtAmount / 100}}</span>
                 </td>
                 <td colspan="2" style="border-right: 1px solid #eeeeee;">
                     <span>{{billDetail.payAmount / 100}}</span>
                 </td>
             </tr>
             <tr>
                 <td colspan="6" style="border-bottom: 1px solid #eeeeee;border-left: 1px solid #eeeeee;border-right: 1px solid #eeeeee;">
                     <span>{{billDetail.remark}}</span>
                 </td>
             </tr>
             <tr>
                 <td colspan="2" style="border-bottom: 1px solid #eeeeee;border-left: 1px solid #eeeeee;">
                     <span>{{billDetail.remark2}}</span>
                 </td>
                 <td colspan="2" style="border-bottom: 1px solid #eeeeee;">
                     <span>{{billDetail.payTime}}</span>
                 </td>
                 <td colspan="2" style="border-bottom: 1px solid #eeeeee;border-right: 1px solid #eeeeee;">
                     <span>{{billDetail.remark3}}</span>
                 </td>
             </tr>
         </table>
     </div>
     <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible_billprint = false"> </el-button>
        <el-button type="primary" @click="handleConfirmPrintBill"></el-button>
     </span>
    </el-dialog>


    <span slot="label" style="font-size: 18px">
      <i class="el-icon-circle-check" style="margin-right: 10px"></i>
    </span>
    <div style="margin: 20px 0 30px 0; text-align:center">
      <el-checkbox label="" value="FINISH" v-model="payStatus1" @change="SearchHasChargeyList"></el-checkbox>
      <el-checkbox
        label=""
        value="ARREARAGE"
        v-model="payStatus2"
        @change="SearchHasChargeyList"
      ></el-checkbox>
      <el-checkbox label="" value="RT" v-model="rtStatus" @change="SearchHasChargeyList"></el-checkbox>
      <el-checkbox label="" value="CANCEL" v-model="status" @change="SearchHasChargeyList"></el-checkbox>
    </div>
    <el-table :data="hasChargeList" style="width: 100%">
      <el-table-column prop="merchantName" label=""></el-table-column>
      <el-table-column prop="payTime" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.status === 'NORMAL'">
              <span v-if="scope.row.payStatus === 'UNPAY'"><span class="indicatorA"><span></span></span></span>
              <span v-if="scope.row.payStatus === 'ARREARAGE'"><span class="indicatorC"><span></span></span></span>
              <span v-if="scope.row.payStatus === 'FINISH'"><span class="indicatorD"><span></span></span></span>
          </span>
          <span v-if="scope.row.status === 'CANCEL'" class="indicatorA"><span></span></span>
        </template>
      </el-table-column>
      <el-table-column prop="clinicTime" label=""></el-table-column>
      <el-table-column prop="remark1" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">{{scope.row.reallyAmount / 100}}</template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">{{scope.row.payAmount / 100}}</template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">{{scope.row.debtAmount / 100}}</template>
      </el-table-column>
      <el-table-column prop="remark" label=""></el-table-column>
      <el-table-column label="" width="250" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="text" @click="handleEditHasCharge(scope.row)"></el-button>
          <el-button size="mini" type="text" @click="handlePrint(scope.row)"></el-button>
          <el-button
            size="mini"
            type="text"
            v-if="scope.row.status === 'NORMAL' && scope.row.rtStatus === 'NO' && scope.row.payAmount "
            @click="handleReturnCharge(scope.row)"
          ></el-button>
          <el-button
            size="mini"
            type="text"
            v-if="scope.row.payStatus === 'ARREARAGE' && scope.row.status === 'NORMAL'"
            @click="handleCollectOverdueBill(scope.row)"
          ></el-button>
          <!-- <el-button
            size="mini"
            type="text"
            v-if="scope.row.status === 'NORMAL'"
            @click="handleCancelHasCharge(scope.row)"
          ></el-button> -->
        </template>
      </el-table-column>
    </el-table>
    <br>
    <el-pagination
      @current-change="handlePageQuery"
      :current-page="currentPage"
      :page-size="limit"
      background
      layout="total, prev, pager, next"
      :total="total"
    ></el-pagination>

    <!--  -->
     <el-dialog
        title=""
        :visible.sync="dialogVisible_return"
        width="30%">
        <br>
        <el-radio-group v-model="refundType" size="mini">
            <el-radio-button label="ALL"></el-radio-button>
            <el-radio-button label="PART"></el-radio-button>
        </el-radio-group>
        <br><br>
        <el-table
            :data="billProjectList"
            ref="tableCheckbox"
            >
            <el-table-column prop="projectName" label="" :key="Math.random()"></el-table-column>
            <el-table-column label="" v-if="refundType !== 'PART'" :key="Math.random()">
                <template slot-scope="scope">
                   {{scope.row.unitPrice / 100}}
                </template>
            </el-table-column>
            <el-table-column prop="itemNum" label="" v-if="refundType !== 'PART'" :key="Math.random()"></el-table-column>
            <el-table-column label="" :key="Math.random()">
                <template slot-scope="scope">
                   {{scope.row.itemDiscountAmount / 100}}
                </template>
            </el-table-column>
             <el-table-column label="" v-if="refundType === 'PART'" :key="Math.random()">
                <template slot-scope="scope">
                   <el-input v-model="scope.row.returnReallyAmount" @keyup.native="number" placeholder="" size="mini"></el-input>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    {{subtractMath(scope.row.itemNum,scope.row.rtNum) }}
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <span>{{subtractMath(scope.row.itemNum,scope.row.rtNum)}}</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="totalBox">
            <span>{{ billDetail.reallyAmount / 100}}</span>
            <span>{{ billDetail.payAmount / 100 }}</span>
            <span>{{ returnReallyAmount / 100}}</span>
            <span>{{ backReallyAmount / 100}}</span>
            <span>{{ backPayAmount / 100 }}</span>
        </div>
        <div class="returnInfo">
            <span>: {{patientDetail.name}}</span>
            <!-- <span>: {{billDetail.remark2}}</span> -->
            <span>: <i>{{ returnReallyAmount /100 }}</i></span>
        </div>
        <el-form :inline="true" ref="formReturnAmount" label-width="100px" :model="formReturnAmount" :rules="formReturnAmountRules" style="width: 100%;margin: 30px 0;" class="demo-form-inline">
            <el-form-item label="" prop="payType">
                <el-select v-model="formReturnAmount.payType" placeholder="">
                    <!-- <el-option label="" value=""></el-option> -->
                    <el-option v-for="item in payTypeList" :key="item.code" :label="item.lableName" :value="item.code"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label=""  prop="refundTimeStr">
                <el-date-picker
                    v-model="formReturnAmount.refundTimeStr"
                    type="datetime"
                    value-format="yyyy-MM-dd HH:mm:ss"
                    placeholder="">
                </el-date-picker>
            </el-form-item>
            <el-form-item label="">
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder=""
                    v-model="formReturnAmount.remark">
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible_return = false "></el-button>
            <el-button type="primary" @click="handleSubmitApply"></el-button>
        </span>
      </el-dialog>
      <el-dialog
        title=""
        :visible.sync="dialogVisible_cancel"
        width="30%">
        <el-form :inline="true" ref="formCancelBill" label-width="100px" :model="formCancelBill" :rules="formCancelBillRules" style="width: 100%;margin: 30px 0;" class="demo-form-inline">
            <el-form-item label="" prop="remark">
                <el-input
                    type="textarea"
                    :rows="2"
                    placeholder=""
                    v-model="formCancelBill.remark"
                    style="width: 400px"
                    >
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible_cancel = false "></el-button>
            <el-button type="primary" @click="handleSubmitCancelBill"></el-button>
        </span>
      </el-dialog>

      <el-dialog
        title=""
        :visible.sync="dialogVisible_edit"
        width="30%">
        <br>
        <el-table
            :data="billProjectList"
            style="width: 100%">
            <el-table-column prop="projectName" label=""></el-table-column>
            <el-table-column prop="unitPrice" label="">
              <template slot-scope="scope">
                {{scope.row.unitPrice / 100}}
              </template>
            </el-table-column>
            <el-table-column prop="itemNum" label=""></el-table-column>
            <el-table-column prop="discountItem" label="">
              <template slot-scope="scope">
                {{scope.row.discountItem / 100}}%
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                {{scope.row.reallyAmount / 100}}
              </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.doctorNo" size="mini" placeholder="" class="pull-right">
                        <el-option
                            v-for="(item,index) in appVuex.shop_doctor"
                            :key="index"
                            :label="item.memberName"
                            :value="item.memberNo">
                        </el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.assistantNo" size="mini" placeholder="" class="pull-right">
                        <el-option
                            v-for="(item,index) in appVuex.shop_nurse"
                            :key="index"
                            :label="item.memberName"
                            :value="item.memberNo">
                        </el-option>
                    </el-select>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-select v-model="scope.row.advisoryNo" size="mini" placeholder="" class="pull-right">
                        <el-option
                            v-for="(item,index) in appVuex.shop_advisory"
                            :key="index"
                            :label="item.memberName"
                            :value="item.memberNo">
                        </el-option>
                    </el-select>
                </template>
            </el-table-column>
        </el-table>
        <div class="returnInfo">
          <span></span>
            <span>: <i>{{billDetail.reallyAmount / 100}}</i></span>
        </div>
        <div class="returnInfo">
            <span>{{billDetail.discountNum / 100}}%</span>
            <span>: <i>{{billDetail.reallyAmount / 100}}</i></span>
        </div>
        <div class="returnInfo">
            <span>{{billDetail.debtAmount / 100}}</span>
            <span>: <i>{{billDetail.payAmount / 100}}</i></span>
        </div>

        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible_edit = false"> </el-button>
            <el-button type="primary" @click="handleSaveEditAlreadyBill"> </el-button>
        </span>
      </el-dialog>
  </div>
</template>

<script>
import {mapState } from 'vuex'
import storage from "@/tools/local_storage.js";
// import Print from '@/tools/Print.js'

export default {
  components: {},
  props: {
    patientDetail: {
      type: Object,
      default: {}
    },
    payTypeList: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      dialogVisible_return: false,
      dialogVisible_cancel: false,
      dialogVisible_edit: false,
      dialogVisible_billprint: false,  // 
      hasChargeList: [],
      payStatus1: '',
      payStatus2: '',
      rtStatus: '',
      status: '',
      billDetail: {},
      billOperate: {},
      billProjectList: [],
      currentBillRow: {},
      refundType: 'ALL',
      currentPage: 1,
      limit: 10,
      total: 1,
      HasChargeSearchOptions: {
        payStatus: '',
        status:'',
        rtStatus:''
      },
      formReturnAmount:{
        payType: '',
        refundTimeStr: '',
        remark: ''
      },
      formReturnAmountRules: {
        payType:[
            { required: true, message: '', trigger: 'change' },
        ],
        refundTimeStr:[
            { required: true, message: '', trigger: 'change' },
        ]
      },
      formCancelBill: {
          remark: ''
      },
      formCancelBillRules: {
            remark:[
              { required: true, message: '', trigger: 'blur' },
            ]
      },
    }
  },
  watch: {},
  computed: {
    ...mapState({
      appVuex: state => state["appVuex"]
    }),

    // 
    returnReallyAmount: (_this) => {
       // 
       if( _this.refundType === 'PART') {
           // 
            let amount = 0
            _this.billProjectList.forEach(item=>{
               amount =  _this.addMath(amount, _this.multiplyMath(item.returnReallyAmount,100))
            })
           return amount
       } else {

           //
           return  _this.subtractMath(_this.billDetail.payAmount,_this.billDetail.rtAmount)
       }

    },

    // 
    backReallyAmount: (_this) => {
        // 
        if( _this.refundType === 'PART') {

            // 
            let amount = _this.subtractMath(_this.billDetail.payAmount, _this.returnReallyAmount)

            // 0-
            if( amount >=  _this.billDetail.reallyAmount  ) {
                return 0
            } else {
                return _this.subtractMath(_this.billDetail.reallyAmount, amount)
            }

        } else {
           // 
           return _this.billDetail.reallyAmount
        }

    },

    // 
    backPayAmount: (_this) => {

       // 
       if( _this.refundType === 'PART') {
            // 
            let amount = 0
            _this.billProjectList.forEach(item=>{
              amount =  _this.addMath(amount,_this.multiplyMath(item.returnReallyAmount,100))
            })

            //  > 
            if(  _this.billDetail.payAmount > amount ) {
                return _this.subtractMath(_this.billDetail.payAmount, amount)
            } else {
                return 0
            }
       } else {
           // 
           return 0
       }
    }


  },
  methods: {

      // 
      number(){
        let returnAmount = 0
        let amount = (this.billDetail.payAmount/100) - (this.billDetail.rtAmount / 100)
        this.billProjectList.forEach((item,index)=>{
            if( Number(item.returnReallyAmount) > item.itemDiscountAmount) {
                item.returnReallyAmount =  item.itemDiscountAmount
            }
            returnAmount += Number(item.returnReallyAmount)

            if( amount < returnAmount) {
                const num = Number(item.returnReallyAmount) - Number(returnAmount - amount)
                if(num >= 0 ){
                    if(num > item.itemDiscountAmount) {
                       item.returnReallyAmount = item.itemDiscountAmount
                    } else {
                       item.returnReallyAmount = num
                    }
                } else {
                    item.returnReallyAmount = 0
                }
            }

        })
  },

    // 
    handlePageQuery(currentPage) {
      this.currentPage = currentPage
      this.getHasChargeList()
    },

    // 
    handleEditHasCharge(row) {
      this.getBillDetail(row,()=>{
          this.dialogVisible_edit = true
      })
    },

    // 
    handleReturnCharge(row) {
       this.getBillDetail(row,()=>{
          this.dialogVisible_return = true
       })
    },

    // 
    handleCollectOverdueBill(row) {
       this.$emit('showCollection',row)
    },

    // 
    handleCancelHasCharge(row) {
      this.dialogVisible_cancel = true
      this.currentBillRow = row
    },

    // 
    handlePrint(row) {
      this.getBillDetail(row,()=>{
        this.dialogVisible_billprint = true
      })
    },

    // 
    handleConfirmPrintBill() {
          this.$print(this.$refs.printBill)
          // Print('#wrap_a', {
          //     onStart: function () {
          //       console.log('onStart', new Date())
          //     },
          //     onEnd: function () {
          //       console.log('onEnd', new Date())
          //     }
          // })
    },

    // 
    SearchHasChargeyList() {
        const { payStatus1,payStatus2,rtStatus,status } = this
        if( payStatus1 && !payStatus2 ) {
            this.HasChargeSearchOptions.payStatus = 'FINISH'
        }
        if( !payStatus1 && payStatus2 ) {
            this.HasChargeSearchOptions.payStatus = 'ARREARAGE'
        }
        if( payStatus1 &&  payStatus2) {
            this.HasChargeSearchOptions.payStatus = ''
        }
        if( !payStatus1 &&  !payStatus2) {
            this.HasChargeSearchOptions.payStatus = ''
        }
        if( rtStatus ) {
           this.HasChargeSearchOptions.rtStatus = 'RT'
        } else {
           this.HasChargeSearchOptions.rtStatus = ''
        }
        if( status ) {
           this.HasChargeSearchOptions.status = 'CANCEL'
        } else {
           this.HasChargeSearchOptions.status = ''
        }
        this.getHasChargeList()
    },


    // 
    handleSaveEditAlreadyBill() {
        const { code } = this.billDetail
        const {
              memberNoGuid,
              memberNoMerchant,
              memberNameMerchant,
              roleEnnames,
              shopNo
        } = storage.get("userInfo")

        const detailsJson = this.billProjectList.map(item=>({
          ...item,
          doctorName: item.doctorNo ? this.appVuex.shop_doctor.find(sssitem=>sssitem.memberNo ===item.doctorNo )['memberName'] : '',
          advisoryName: item.advisoryNo ? this.appVuex.shop_advisory.find(sssitem=>sssitem.memberNo ===item.advisoryNo )['memberName'] : '',
          assistantName: item.assistantNo ?this.appVuex.shop_nurse.find(sssitem=>sssitem.memberNo ===item.assistantNo )['memberName'] : '',
        }))
        this.$http.post('/api/hx/bill/editMember.do',{
          code,
          UpdateId: memberNoGuid,
          detailsJson: JSON.stringify(detailsJson)
        }).then(res=>{
              const { result, errorMessage, returnObject } = res.data
              if (result) {
                  this.$message({
                      type: "success",
                      message: ''
                  })
                  this.getHasChargeList()
                  this.dialogVisible_edit = false
              } else {
                  this.$message({
                      type: "error",
                      message: errorMessage
                  });
              }
        })

    },

    // 
    handleSubmitCancelBill() {
        const {
                memberNoGuid,
                memberNameGuid,
                memberNoMerchant,
                memberNameMerchant,
                roleEnnames,
                shopNo
            } = storage.get("userInfo")
            const formData = {
                code: this.currentBillRow.code,
                memberNoGuid,
                memberNameGuid,
                remark: this.formCancelBill.remark
            }
            this.$refs['formCancelBill'].validate((valid) => {
                if (valid) {
                    this.$http.post('/api/hx/bill/cancel.do',{
                        ...formData
                    }).then(res=>{
                            const { result, errorMessage } =  res.data
                            if(result){
                                this.$message({
                                    type: "success",
                                    message: '!'
                                })
                                this.getHasChargeList()
                                this.dialogVisible_cancel = false
                                this.currentBillRow = {}
                            }else{
                                this.$message({
                                type:'error',
                                message: errorMessage
                                })
                            }
                    })

            } else {
              console.log('error submit!!');
              return false;
            }
        })
    },

    // 
    handleSubmitApply() {
        const {
            memberNoGuid,
            memberNameGuid,
            memberNoMerchant,
            memberNameMerchant,
            roleEnnames,
            shopNo
        } = storage.get("userInfo")
        const {code,payAmount, refundCode, billOperate} = this.billDetail
        const { payType,refundTimeStr ,remark} = this.formReturnAmount
        const detailsJson = this.billProjectList.map(item=>({
            projectCode: item.projectCode,
            projectName: item.projectName,
            returnNum: this.refundType === 'PART' ? 0 : item.itemNum - item.rtNum,
            returnAmount: this.refundType === 'PART' ? item.returnReallyAmount : 0
        }))

        if (payType == '') {
          this.$message.error('')
          return
        }
        this.$refs['formReturnAmount'].validate((valid) => {
            if (valid) {
                const formData = {
                    billCode: code,
                    payType,
                    payTypeName: this.payTypeList.find(item=>item.code === payType)['lableName'],
                    rtAmount: Number(this.returnReallyAmount),
                    refundGdNo: this.patientDetail.code,
                    refundGdName: this.patientDetail.name,
                    refundTimeStr,
                    refundType: this.refundType,
                    memberNoGuid,
                    memberNameGuid,
                    remark,
                    detailsJson: JSON.stringify(detailsJson)
                }

                this.$http.post('/api/hx/bill/refund/apply.do',{
                    ...formData
                }).then(res=>{
                    const { result, errorMessage, returnObject } = res.data
                    if (result) {
                        this.$message({
                            type: "success",
                            message: ''
                        })
                        this.dialogVisible_return = false
                        this.billDetail = {}
                        this.billOperate = {}
                        this.billProjectList  = []
                    } else {
                        this.$message({
                            type: "error",
                            message: errorMessage
                        });
                    }
                })
                this.dialogVisible_return = false
                this.isEditWaitApproval = false
            } else {
              console.log('error submit!!');
              return false;
            }
        })

      },

    // 
    getHasChargeList() {
        const { currentPage , limit } = this
        const { payStatus,status,rtStatus } = this.HasChargeSearchOptions
        const {
            memberNoGuid,
            memberNoMerchant,
            memberNameMerchant,
            roleEnnames,
            shopNo
        } = storage.get("userInfo")
        const { code } = this.patientDetail
        this.$http.post('/api/hx/bill/haspayList.do',{
            start: (currentPage - 1) * limit,
            limit: limit,
            merchantNo: memberNoMerchant,
            patientNo: code,
            payStatus,
            status,
            rtStatus
        }).then(res=>{
            const { result, errorMessage, returnObject } = res.data;
            if (result) {
                const { total, rows } = returnObject
                this.hasChargeList = rows
                this.total = total
            } else {
                this.$message({
                    type: "error",
                    message: errorMessage
                })
            }

        })
    },

    // 
      getBillDetail(row,callback) {
        const {
                memberNoGuid,
                memberNoMerchant,
                memberNameMerchant,
                roleEnnames,
                shopNo
          } = storage.get("userInfo")
          this.$http.post('/api/hx/bill/detail.do',{
              merchantNo:memberNoMerchant,
              billCode: row.code
          }).then(res=>{
                const { result, errorMessage, returnObject } =  res.data
                if(result){
                    this.billDetail = returnObject
                    this.billOperate = returnObject.billOperate
                    this.billProjectList =  returnObject.details.map(item=> ({
                        ...item,
                        returnReallyAmount: item.itemDiscountAmount / 100
                    }))
                    this.number()
                    if(callback)callback()
                }else{
                    this.$message({
                      type:'error',
                      message: errorMessage
                    })
                }
          })
      },
  },
  created() {},
  mounted() {
    this.getHasChargeList()
    console.log(this.billProjectList)
  }
};
</script>
<style lang="less" scoped>
.totalBox{
    text-align: center;
    span{
        display: inline-block;
        margin: 20px 30px 20px 0;
        font-size: 18px;
    }
}
.returnInfo{
    padding: 20px 0;
    border-top: 1px solid #eeeeee;
    border-bottom: 1px solid #eeeeee;
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    span{
        display: inline-block;
        margin-right: 100px;
        i{
            font-size: 24px;
            color: red;
        }
    }
}
.printBox{
    width: 100%;
    table{
        width: 100%;
    }
    tr td {
        padding: 10px;
        text-align: left;
        font-size: 12px;
    }
}
</style>



